:root {
  font-size: 18px;
}

.main{
  position: relative;
  background-image: linear-gradient(0deg, rgb(224, 215, 133) 0%, palegoldenrod 40%, cornsilk 90%);
  width: 100%;
  margin: 0 !important ;
}

.row{
  align-items: center;
}

.row:last-child{
  padding-bottom: 20px;
}

.navbar-brand{
  font-size: 1em !important;
}

.badge-no-text{
  font-size: 1em;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.bwarning{
background-color: #ffc107;
color: black;
font-weight: 700;
border-radius: 5px;
}

.bprimary{
  background-color: #007bff;
  color: white;
  font-weight: 700;
  border-radius: 5px;
}

.plus, .minus{
  font-size: 1.5em;
  font-weight: bold;
}

.respMsg {
  background-color: rgb(241, 241, 241);
  text-align: center;
  color: forestgreen;
}

.respBgr0{
  background-color: #ffc107;
  color: black;
  font-weight: 500;
}
.respBgrWhSal{
  background-color: mediumseagreen;
  color: white;
  font-weight: 600;
}

.n1{
  color: slategray;
}

.n2{
 color: #007bff; 
}

.n3{
  color: darkgoldenrod; 
 }

.sm-caps {
  font-variant: small-caps;
  color: white;
  border-radius: 5px;
  padding: 2px 7px;
  letter-spacing: 0.3px;
}

.prodW{
  height: 30%;
  text-align: center;
  align-items: center;
}

.prodName {
  padding: 2px 5px;
  font-size: 1em;
  line-height: 1.2;
  font-weight: 700;
  width: 8em;
  border-radius: 5px;
}

.unit{
  padding: 2px 5px !important;
  font-size: 0.9em;
  line-height: 1.2;
  font-weight: 700;
  border-radius: 5px;
}

.priceW{
  min-width: 3em;
  text-align: center;
}

.price {
  font-weight: bold;
  min-width: 3em;
  border-radius: 5px;
}

.bgr1 {
  background-color: slategray;
}

.fCol1 {
  background-color: white;
  color: rgb(39, 39, 41);
}

.fCol2 {
  background-color: white;
  color: chocolate;
}

.mainTitle {
  color: darkslategray;
  font-variant: small-caps;
  font-weight: 900;
}

.dev-info {
  display: block;
  background-color: rgb(194, 194, 194);
  color: rgb(47, 61, 61);
}

.dev-box{
  font-size: 0.9em;
  line-height: 1.2;
}

.add-btn {
  font-size: 0.8em !important;
  align-self: center;
}

.add-submit{
font-size: 0.8em !important;
align-self: center;
}

.formAdd {
  color:midnightblue;
  font-weight: 700;
}

.formWrapp{
  overflow: hidden;
}

.visible-form{
  margin-top: 0px;
  opacity: 1;
  /* transition: margin-top 1s 0.5s linear, opacity 0.1s 0.5s linear; */
  transition: margin-top 1.8s 0.3s linear, opacity 0.5s linear;
}

.hidden-form{
  opacity: 0;
  margin-top: -150%;
  transition: margin-top 2s 0.5s linear, opacity 1s 2s linear;
}

.dialog-box {
  color: black;
  position: absolute;
  z-index: 100;
  left: 50%;
  top: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  max-width: 80%;
}

.dialog-question{
  color:rgb(39, 39, 41);
}

.confirm-age {
  color: darkblue;
}

.btn-close{
  text-align: right;
  margin-right: -50px;
  margin-bottom: 10px;
  margin-top: -13px;
  font-size: 0.8em !important;
}

.btn-close2{
  text-align: right;
  margin-right: -35px;
  margin-bottom: 15px;
  margin-top: -10px;
  font-size: 0.8em !important;
}

.close-btn{
  background-color: gainsboro !important;
}

.close-btn:hover{
  background-color: rgb(194, 194, 194) !important;
}

.offer{
  text-align: center;
}

.specialOffer {
  color: mediumvioletred;
}

.todayOffer {
  font-variant-caps: titling-caps;
  color: indigo;
}

.reset {
  background-color: rgb(228, 110, 0) !important;
  color: white !important;
}

.tippy-box {
  background-color: rgb(139, 139, 139) !important;
}

.tippy-arrow {
  color: rgb(139, 139, 139) !important;
}

.footer {
  color: rgb(22, 22, 46);
}

@media all and (min-width: 576px){
  .unit{
      width: 55px !important;
  }

  .prodW{
      width: 65px !important;
  }
}

@media all and (max-width: 575px){
  .unit {
      width: 100% !important;
  }

  .prodW{
    width: 90% !important;
}
}